.grid {
 width: 100%;
 height: 100%;
 display: flex;
 z-index: 0;
}

.tables {
 width: 100%;
 position: relative;
 overflow-y: scroll;
 overflow-x: hidden;
 z-index: 0;
}

.react-grid-layout {
 z-index: 0;
 transition: 0.2s ease-in;
 position: absolute;
 height: 100%;
 width: 100%;
}

.react-grid-item {
 z-index: 0;
 transition: all 200ms ease;
 transition-property: left, top;
}

.react-grid-item.react-grid-placeholder {
 background: red;
 opacity: 0.2;
 transition-duration: 100ms;
 z-index: 2;
 -webkit-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 -o-user-select: none;
 user-select: none;
}

.DragHandle {
 height: 42px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 cursor: move;
}

.DragHandle p {
 margin-left: 5px;
 font-size: 12px;
}

.DragHandle__img {
 width: 52px;
 height: 95%;
 display: flex;
 justify-content: center;
 align-items: center;
 border-radius: 5px 5px 0 0;
 border: 1px solid #292933;
}

.DragHandle__img:hover {
 cursor: pointer;
 border: 1px solid #00C9FF !important;
 box-shadow: 0 0 6px 0 #00C9FF !important;
}

.DragHandle img {
 width: 25px;
 margin-right: -15px;
}

